/* Mobile-first global styles for a wedding invite (works without Tailwind)
   - Keep base canvas responsive but lock content width to a comfy mobile size
   - Light system font stack for performance
   - Design tokens via CSS variables
*/

/* === Design tokens === */
:root {
  --bg: #ffffff;
  --bg-soft: #f7f7fb;
  --text: #111113;
  --text-muted: #6b7280; /* gray-500ish */
  --primary: #111113;    /* near-black for high contrast */
  --secondary: #f2f2f6;  /* soft gray */
  --accent: #ffdfe7;     /* soft pink accent */
  --glass: rgba(255,255,255,0.7);
  --border: rgba(0,0,0,0.12);

  --radius: 16px;
  --radius-lg: 24px;
  --shadow: 0 6px 18px rgba(0,0,0,0.06);
  --shadow-soft: 0 2px 10px rgba(0,0,0,0.05);

  --space-1: 4px;
  --space-2: 8px;
  --space-3: 12px;
  --space-4: 16px;
  --space-5: 20px;
  --space-6: 24px;
  --space-8: 32px;
  --space-10: 40px;
}

/* === CSS reset / base === */
* { box-sizing: border-box; }
html, body, #root { height: 100%; }
html { -webkit-text-size-adjust: 100%; }
body {
  margin: 0;
  background: var(--bg);
  color: var(--text);
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, "Apple SD Gothic Neo", "Malgun Gothic", sans-serif;
  line-height: 1.55;
}
img, picture, video, canvas, svg { display: block; max-width: 100%; }
button, input, select, textarea { font: inherit; color: inherit; }
a { color: inherit; text-decoration: none; }

/* Respect reduced motion */
@media (prefers-reduced-motion: reduce) {
  * { animation: none !important; transition: none !important; }
}

/* === Layout helpers === */
.container {
  /* Content stays mobile-sized even on desktop */
  width: 100%;
  max-width: 420px; /* mobile comfort width */
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
.section { padding: var(--space-10) 0; }
.section + .section { padding-top: 0; }

.center { text-align: center; }
.muted { color: var(--text-muted); }

/* Soft backdrop blobs for hero (optional) */
.hero-bg {
  position: relative;
  background:
    radial-gradient(90% 60% at 10% 10%, #f9f9ff 0%, transparent 60%),
    radial-gradient(70% 50% at 95% 0%, #eef7ff 0%, transparent 60%);
}
.hero-blob::before,
.hero-blob::after {
  content: "";
  position: absolute; inset: auto auto auto auto;
}
.hero-blob::before {
  top: -70px; left: -70px; width: 280px; height: 280px;
  background: #ffe7ec; filter: blur(24px); opacity: .7; border-radius: 999px;
}
.hero-blob::after {
  top: -60px; right: -60px; width: 320px; height: 320px;
  background: #e7f4ff; filter: blur(24px); opacity: .7; border-radius: 999px;
}

/* === Typography scale (mobile-first) === */
h1 { font-size: 28px; line-height: 1.15; margin: 0; }
h2 { font-size: 22px; line-height: 1.2;  margin: 0 0 var(--space-4); }
h3 { font-size: 18px; line-height: 1.25; margin: 0 0 var(--space-3); }
p  { margin: 0 0 var(--space-3); }
.small { font-size: 13px; }
.eyebrow { font-size: 11px; letter-spacing: .18em; text-transform: uppercase; color: var(--text-muted); }

/* Bump type slightly on wide screens while keeping mobile width */
@media (min-width: 640px) {
  h1 { font-size: 36px; }
  h2 { font-size: 26px; }
}

/* === Cards & glass === */
.card {
  background: var(--glass);
  backdrop-filter: saturate(120%) blur(6px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow);
}
.card-header { padding: var(--space-6); border-bottom: 1px solid var(--border); }
.card-content { padding: var(--space-6); }

/* === Buttons === */
.btn {
  display: inline-flex; align-items: center; justify-content: center;
  gap: 8px; height: 40px; padding: 0 16px;
  border-radius: 999px; border: 1px solid transparent;
  font-size: 14px; font-weight: 500; cursor: pointer;
  transition: transform .06s ease, background-color .2s ease, opacity .2s ease;
}
.btn:active { transform: translateY(1px); }
.btn-primary { background: var(--primary); color: white; }
.btn-primary:hover { opacity: .9; }
.btn-secondary { background: var(--secondary); color: var(--text); }
.btn-outline { background: white; border-color: var(--border); }

/* === Images === */
.rounded { border-radius: var(--radius); }
.rounded-lg { border-radius: var(--radius-lg); }
.shadow { box-shadow: var(--shadow-soft); }
.cover { width: 100%; height: 100%; object-fit: cover; }

/* === Fixed action bar (floating) === */
.floating-bar {
  position: fixed; left: 50%; bottom: 16px; transform: translateX(-50%);
  z-index: 50; background: rgba(255,255,255,.85);
  backdrop-filter: blur(8px);
  border: 1px solid var(--border);
  border-radius: 999px; box-shadow: var(--shadow);
  display: flex; gap: 8px; padding: 6px 8px;
}

/* === Utility spacing (a few only) === */
.mt-1 { margin-top: var(--space-1); }
.mt-2 { margin-top: var(--space-2); }
.mt-3 { margin-top: var(--space-3); }
.mt-4 { margin-top: var(--space-4); }
.mt-6 { margin-top: var(--space-6); }
.mt-8 { margin-top: var(--space-8); }

/* === Map placeholder === */
.map-frame { height: 220px; background: var(--bg-soft); border: 1px dashed var(--border); border-radius: var(--radius); display: grid; place-items: center; color: var(--text-muted); }

/* === List rows (for info & accounts) === */
.list { border-top: 1px solid var(--border); }
.list .row { display: flex; gap: 12px; padding: 12px 0; border-bottom: 1px solid var(--border); }
.list .label { color: var(--text-muted); font-size: 13px; min-width: 64px; }
.list .value { font-weight: 500; }
